<template>
  <div class="othersContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/deploy' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form :model="ruleForm" ref="ruleForm" label-width="240px" class="demo-ruleForm">
        <el-form-item label="" prop="bname" required>
          <el-input v-model="ruleForm.bname" placeholder="20"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mainAccount">
          <el-input v-model="ruleForm.mainAccount" placeholder="3-10"></el-input>
        </el-form-item>
        <p class="ptips"></p>
        <el-form-item label="" prop="address">
          <label>
            <el-cascader
              v-model="ruleForm.area"
              :options="areaOption"
              @change="handleChange"
              clearable
            ></el-cascader>
          </label>
          <el-input v-model="ruleForm.address" placeholder="" id="suggestId" style="marginTop:15px;"></el-input>
        </el-form-item>
        <el-form-item style="marginTop:-15px;">
          <baidu-map
            :center="center"
            :zoom="zoom"
            @ready="handler"
            class="baiduMap"
            :scroll-wheel-zoom="true"
            id="l-map"
          >
            <!-- <bm-view class="map"></bm-view> -->
            <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
            <bm-local-search
              :keyword="ruleForm.address"
              :auto-viewport="true"
              :panel="false"
              @searchcomplete="getGps"
            ></bm-local-search>
          </baidu-map>
        </el-form-item>
        <el-form-item label="" prop="personInCharge" required>
          <el-input v-model="ruleForm.personInCharge" placeholder="20"></el-input>
        </el-form-item>
        <el-form-item label="" prop="picPhone" required>
          <el-input v-model="ruleForm.picPhone"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-date-picker
            v-model="ruleForm.validityTime"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="state" required>
          <el-select v-model="ruleForm.state" placeholder="">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input
            type="textarea"
            v-model="ruleForm.description"
            placeholder="500"
            maxlength="500"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="deviceNumLimit" required>
          <el-input v-model="ruleForm.deviceNumLimit"></el-input>
        </el-form-item>
        <div class="second">
          <el-form-item label="" required>
            <div class="checkGroup">
              <el-checkbox label="" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span></span>
            <i>1440</i>
          </el-form-item>
          <el-form-item label="" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i></i>
          </el-form-item>
          <el-form-item label="app" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>wifiwifi</i>
          </el-form-item>
          <el-form-item label="" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="app" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i></i>
          </el-form-item>
        </div>
        <el-form-item label="IP" prop="serverHost" required>
          <el-input v-model="ruleForm.serverHost"></el-input>
        </el-form-item>
        <el-form-item label="MAC" prop="serverMac" required>
          <el-input v-model="ruleForm.serverMac"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        state: "",
        description: "",
        deviceNumLimit: "",
        serverHost: "",
        serverMac: "",
        validityTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 60,
        upload: "",
        videoLimit: 1000,
        app: "",
        appLimit: 500,
        switch: "",
        switchLimit: 20,
        log: "",
        appLogLimit: 20,
        save: "",
        saveLimit: 100
      },
      areaOption: regionData,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      location: "",
      keyword: "",
      arealocation: ""
    };
  },
  methods: {
    ...mapActions(["addThirdAsync", "deployFormAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.arealocation = province + city + area;
      // console.log(this.arealocation);
      this.ruleForm.address = this.arealocation;
      // console.log(this.ruleForm.address);
    },
    //
    finalTime() {
     if (this.ruleForm.time == "") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //
    videoUpload() {
      if (this.ruleForm.upload == "") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //app
    appUpload() {
     if (this.ruleForm.app == "") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "") {
        this.appVideo = 2;
        this.three = false;
      }
    },
    //
    logSwitch() {
      if (this.ruleForm.switch == "") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "") {
        this.switchLog = 2;
        this.four = false;
      }
    },
    //app
    appLog() {
       if (this.ruleForm.log == "") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "") {
        this.logApp = 2;
        this.five = false;
      }
    },
    //
    savation() {
      if (this.ruleForm.save == "") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "") {
        this.saveSet = 1;
        this.six = false;
      }
    },
    async add() {
      let bname = this.ruleForm.bname;
      let mainAccount = this.ruleForm.mainAccount;
      let locations = this.ruleForm.address;
      let lat = this.gpsLocation.lat;
      let lon = this.gpsLocation.lng;
      //
      const data = await this.addThirdAsync({
        ...this.ruleForm,
        address: locations,
        longitude: lon,
        latitude: lat
      });
      if (data.code == 200) {
        this.$message("");
        this.deployFormAsync();
      } else if (data.code == 202) {
        this.$message("");
      } else if (data.code == 207) {
        this.$message("");
      }
    },
    //
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
       console.log(results.Br[0].point);
      this.gpsLocation = results.Br[0].point;
    }
  }
};
</script>

<style scoped>
.othersContainer {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.form p {
  color: rgba(153, 153, 153, 1);
}
.ptips {
  color: rgba(153, 153, 153, 1);
  margin-left: 240px;
  font-size: 14px;
}
/*  */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
</style>

<style>
.othersContainer .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.othersContainer .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.othersContainer .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}

.form .el-form .el-input__inner {
  width: 400px;
}
.form .el-form .el-form-item__content {
  width: 800px;
}
.form .el-form .address {
  margin-top: 15px;
}
.othersContainer .el-textarea {
  width: 400px;
}
</style>