import axios from "axios";
import qs from "qs";

export default {
    //
    async changeName(params) {
        let data = await axios.post("/login/updateUserName", qs.stringify(params));
        return data;
    },
    //
    async changePassword(params) {
        let data = await axios.post("/login/updatePassWord", qs.stringify(params));
        return data;
    },
    //--
    async allCompany(params) {
        let {
            data
        } = await axios.post("/enterprise/handleSelectAllBusinessManagerByState", qs.stringify(params));
        return data;
    },
    //--
    async search(params) {                                            
        let {data} = await axios.get("/enterprise/handleSelectBusinessManagerByBname", {params});            
        return data;
    },
    //--id
    async allCharts(params) {                                            
        let {data} = await axios.get("remote/handleSelectClientCount", {params});            
        return data;
    },
    //
    async allLongitude(params) {                                            
        let {data} = await axios.get("/enterprise/handleSelectAllBusinessCoordinate", {params});            
        return data;
    },
    //
    async uploadFile(params) {
        let {data} = await axios.post("http://192.168.0.180:8081/loophole/fileUploading",params, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        });
        return data;
    },
    //logometa
    async getLogeMeta() {
        let data = await axios.get('/system/handleSelectPageSetting');
        // console.log(data)
        return data; 
    },
    //
    async newsStatus(params) {                                            
        let {data} = await axios.get("/system/handleAllUnReadTopMessages", {params});            
        return data;
    },
}