import companyApi from "../services/companyService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 6,
        list: [],
        total: 4,
        businessManager: {},
        diySettingMapper: {},
        queryData: {},
        location: "",
        start: "",
        end: "",
        coop: "",
        writePwd: '',
        writeName: ""
    },
    mutations: {
        setData(state, payload) {
            //total            
            Object.assign(state, {
                list: payload.businessManager.list,
                total: payload.totalCount
            })
        },

        checkData(state, payload) {
            Object.assign(state, {
                businessManager: payload.businessManager,
                diySettingMapper: payload.diySettingMapper
            })
        },
        //

        initLocation(state, payload) {
            Object.assign(state, {
                location: payload
            })
        },
        initStart(state, payload) {
            Object.assign(state, {
                start: payload
            })
        },
        initEnd(state, payload) {
            Object.assign(state, {
                end: payload
            })
        },
        initCoop(state, payload) {
            Object.assign(state, {
                coop: payload
            })
        },
        initWriteName(state, payload) {
            Object.assign(state, {
                writeName: payload
            })
        },
        initWritePwd(state, payload) {
            Object.assign(state, {
                writePwd: payload
            })
        },
        searchForm(state, payload) {                        
            Object.assign(state, {
                list: payload.businessManager.list,
                total: payload.businessManager.total
            })
        },
        //
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
        //
        query(state, payload) {                        
            Object.assign(state, {
                queryData: payload
            })
        }
    },
    actions: {
        //
        async formAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            }            
            const {data} = await companyApi.companyForm(params);
            context.commit("setData", data)
            return data;
        },
        //
        async searchAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                startTime: context.state.start,
                endTime: context.state.end,
                status: context.state.coop,
                address: context.state.location,
                bname: context.state.writeName,
                mainAccount: context.state.writePwd,
            };            
            const {data} = await companyApi.search(params);
            context.commit("searchForm", data);
        },
        //
        async companyDetailAsync(context, payload) {
            const {
                data
            } = await companyApi.companyDetail({
                bid: payload
            });
            context.commit("checkData", data)
            return data
        },
        //-
        async reStartAsync(context, payload) {
            const data = await companyApi.reStart(payload);
            return data;
        },
        //-
        async pauseAsync(context, payload) {
            const data = await companyApi.pause(payload);
            return data;
        },
        //
        async locationAsync(context, payload) {
            const {
                data
            } = await companyApi.location(payload);
            return data;
        },
        //
        async rePasswordAsync(context, payload) {
            const data = await companyApi.rePassword(payload);
            return data;
        },
        //
        async additionAsync(context, payload) {
            const data = await companyApi.addition(payload)
            return data;
        },
        //
        async updateCompanyAsync(context, payload) {
            const data = await companyApi.updateCompany(payload)
            return data;
        },
        //
        async statisticAsync(context, payload) {
            const 
                {data}
            = await companyApi.statistic(payload)
            context.commit("query", data)
            return data;
        },
        //
        async companyStorageAsync(context, payload) {
            const {data} = await companyApi.companyStorage(payload)
            return data;
        },
        //
        async clearStorageAsync(context, payload) {
            const data = await companyApi.clearStorage(payload)            
            return data;
        },
        //
        async judgementAsync(context, payload) {
            const data = await companyApi.judgement(payload)            
            return data;
        },
    }
}