<template>
  <div class="addContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        label-width="220px"
        class="demo-ruleForm"
        :rules="rules"
      >
        <div class="first">
          <el-form-item label="" prop="bname">
            <el-input v-model="ruleForm.bname" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mainAccount" >
            <el-input v-model="ruleForm.mainAccount" @input="ifRepeat" placeholder="3-10"></el-input>
          </el-form-item>
          <p class="ptips"></p>
          <el-form-item label="" prop="address">
            <label>
              <el-cascader
                v-model="ruleForm.area"
                :options="areaOption"
                @change="handleChange"
                clearable
              ></el-cascader>
            </label>
            <el-input
              v-model="ruleForm.address"
              placeholder=""
              @change="adress"
              id="suggestId"
            ></el-input>
          </el-form-item>
          <el-form-item style="marginTop:-15px;">
            <baidu-map
              :center="center"
              :zoom="zoom"
              @ready="handler"
              class="baiduMap"
              :scroll-wheel-zoom="true"
              id="l-map"
            >
              <!-- <bm-view class="map"></bm-view> -->
              <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
              <bm-local-search
                :keyword="ruleForm.address"
                :auto-viewport="true"
                :panel="false"
                @searchcomplete="getGps"
              ></bm-local-search>
            </baidu-map>
          </el-form-item>
          <el-form-item label="" prop="principal">
            <el-input v-model="ruleForm.principal" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="ruleForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="dateTime">
            <el-date-picker
              v-model="ruleForm.dateTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="ruleForm.status" placeholder="" @change="sta">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="others">
            <el-input type="textarea" v-model="ruleForm.others" placeholder="500" maxlength="500"></el-input>
          </el-form-item>
          <el-form-item label="" prop="limit">
            <el-input v-model="ruleForm.limit" ></el-input>
          </el-form-item>
        </div>
        <div class="second">
          <el-form-item label="" required>
            <div class="checkGroup">
              <el-checkbox label="" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span></span>
            <i>1440</i>
          </el-form-item>
          <el-form-item label="" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i></i>
          </el-form-item>
          <el-form-item label="app" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>wifiwifi</i>
          </el-form-item>
          <el-form-item label="" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="app" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i></i>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="add"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { BmlMarkerClusterer } from "vue-baidu-map";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        principal: "",
        phone: "",
        status: "",
        others: "",
        limit: "",
        dateTime: "",
