      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      location: "",
      keyword: "",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 20, message: "20", trigger: "blur" }
        ],
        mainAccount: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3-10",
            trigger: "blur"
          }
        ],
        area: [
          { required: true, message: "", trigger: "blur" }
        ],
        address: [
          { required: true, message: "", trigger: "blur" }
        ],
        principal: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "", trigger: "blur" }
        ],
        dateTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "blur" }
        ],
        others: [{ max: 500, message: "500", trigger: "blur" }],
        limit: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {},
  methods: {
    ...mapActions(["additionAsync", "formAsync","judgementAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.arealocation = province + city + area;
      this.site = value;
      // console.log(province);
      this.ruleForm.address = this.arealocation;
      // console.log(this.ruleForm.address);
    },
    adress() {
      // console.log(this.ruleForm.address);
    },
    //
    sta() {
      this.staus = this.ruleForm.status;
    },
    //
    finalTime() {
      if (this.ruleForm.time == "") {
        this.videoTime = 0;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //
    videoUpload() {
      if (this.ruleForm.upload == "") {
        this.uploadVideo = 0;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //app
    appUpload() {
      if (this.ruleForm.app == "") {
