    //     });
    //     local.search(myValue);
    //   }
    // }

    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);

      // API
      // function G(id) {
      //   return document.getElementById(id);
