<style scoped>
.addContainer {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.ptips {
  color: rgba(153, 153, 153, 1);
  margin-left: 220px;
  font-size: 14px;
}
.form .el-form .finalVideo {
  display: flex;
}

/*  */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
.map {
  width: 400px;
  height: 400px;
}
</style>
<style>
.addContainer .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.addContainer .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.addContainer .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}

.form .el-form .first .el-input__inner {
  width: 400px;
}
.form .el-form .second .el-input__inner {
  width: 120px;
}
.form .el-form .second .el-input {
  width: 120px;
  margin-left: 20px !important;
}
.form .el-form .first .el-form-item__content {
  width: 800px;
}
.form .el-form .second .el-form-item__content {
  width: 1200px;
}
.form .el-form .address {
  margin-top: 15px;
}
.addContainer .el-textarea {
  width: 400px;
}
.addContainer .el-cascader {
    margin-bottom: 20px;
}
.baiduItem{
  margin-top: -15px;
}
/*  */
.BMap_cpyCtrl {
  display: none;
}
.anchorBL {
  display: none;
}
</style>