import axios from "axios";
import qs from "qs";

export default{
    //-
    async mesCenter(params) {                                    
        let {data} = await axios.get("/system/handleQueryTopMessages", {params});            
        return data;
    },
     //-
     async delet(params) {                        
        let {data} = await axios.post("/system/handleUpdateReadTopMessages", qs.stringify(params));                          
        return data;
    },
    //--
    async otherSet(params) {               
        let {data} = await axios.post("/system/handleQueryDefaultSetting", qs.stringify(params));                          
        return data;
    },
    //--
    async otherUpdate(params) {                               
        let {data} = await axios.post("/system/handleUpdateDefaultSetting", qs.stringify(params));                          
        return data;
    },
    //
    async newsStatus(params) {                                            
        let {data} = await axios.get("/system/handleAllUnReadTopMessages", {params});            
        return data;
    },
}