<template>
  <div class="otherSet">
    <p></p>
    <div class="otherform">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        label-width="270px"
        class="demo-ruleForm"
        label-position="left"
      >
        <el-form-item label="" prop="longtime">
          <el-radio-group v-model="ruleForm.longtime" @change="finalTime">
            <el-radio label=""></el-radio>
            <el-radio label="" ></el-radio>
          </el-radio-group>
          <el-input placeholder="500" :disabled="one" v-model="timeLimit"></el-input>
          <span></span>
          <i>1440</i>
        </el-form-item>
        <el-form-item label="" prop="video">
          <el-radio-group v-model="ruleForm.video" @change="autoUpload">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="1000" :disabled="two" v-model="videoLimit"></el-input>
          <span>MB</span>
          <i></i>
        </el-form-item>
        <el-form-item label="app" prop="appvideo">
          <el-radio-group v-model="ruleForm.appvideo" @change="appUpload">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="1000" :disabled="three" v-model="appLimit"></el-input>
          <span>MB</span>
          <i>wifiwifi</i>
        </el-form-item>
        <el-form-item label="" prop="log">
          <el-radio-group v-model="ruleForm.log" @change="logSwitch">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="four" v-model="switchLimit"></el-input>
          <span></span>
          <i>550</i>
        </el-form-item>
        <el-form-item label="app" prop="applog">
          <el-radio-group v-model="ruleForm.applog" @change="logApp">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="five" v-model="appLogLimit"></el-input>
          <span></span>
          <i>550</i>
        </el-form-item>
        <el-form-item label="" prop="remind">
          <el-radio-group v-model="ruleForm.remind" @change="companyRe">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="six" v-model="companyDate"></el-input>
          <span></span>
          <i></i>
        </el-form-item>
        <el-form-item label="" prop="remindOther">
          <el-radio-group v-model="ruleForm.remindOther" @change="otherRe">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="seven" v-model="thirdCompany"></el-input>
          <span></span>
          <i></i>
        </el-form-item>
        <el-form-item label="" prop="warn">
          <input type="text" placeholder="400" class="warn" v-model="ruleForm.warn" />
          <span>GB</span>
          <i></i>
        </el-form-item>
          <p>*</p>
        <el-form-item>
          <el-button type="primary" @click="update"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("system");
export default {
  data() {
    return {
      ruleForm: {
        longtime: "",
        video: "",
        appvideo: "",
        log: "",
        applog: "",
        remind: "",
        remindOther: "",
        warn: 500
      },
      timeLimit: 60,
      one: true,
      videoLimit: 500,
      two: true,
      appLimit: 500,
      three: true,
      switchLimit: 5,
      four: true,
      appLogLimit: 5,
      five: true,
      companyDate: 30,
      six: true,
      thirdCompany: 30,
      seven: true
    };
  },
  async mounted() {
    const data = await this.otherSetAsync();    
    //
    if (data.vedioTimeLimit == 0) {
      this.ruleForm.longtime = "";
      this.timeLimit = 0;
    } else {
      this.ruleForm.longtime = "";
      this.one = false;
      this.timeLimit = data.vedioTimeLimit;
    }
    //
    if (data.isAutoUpload == 0) {
      this.ruleForm.video = "";
      this.videoLimit = 0;
    } else {
      this.ruleForm.video = "";
      this.two = false;
      this.videoLimit = data.isAutoUpload;
    }
    //app
    if (data.dispatcherUploadLimit == 0) {
      this.ruleForm.appvideo = "";
      this.appLimit = 0;
    } else {
      this.ruleForm.appvideo = "";
      this.three = false;
      this.appLimit = data.dispatcherUploadLimit;
    }
    //
    if (data.appLog == 0) {
      this.ruleForm.log = "";
      this.switchLimit = 0;
    } else {
      this.ruleForm.log = "";
      this.four = false;
      this.switchLimit = data.appLog;
    }
    //app
    if (data.dispatcherLog == 0) {
      this.ruleForm.applog = "";
      this.appLogLimit = 0;
    } else {
      this.ruleForm.applog = "";
      this.five = false;
      this.appLogLimit = data.dispatcherLog;
    }
    //
    if (data.tipTime == 0) {
      this.ruleForm.remind = "";
      this.companyDate = 0;
    } else {
      this.ruleForm.remind = "";
      this.six = false;
      this.companyDate = data.tipTime;
    }
    //
    if (data.thirdTipTime == 0) {
      this.ruleForm.remindOther = "";
      this.thirdCompany = 0;
    } else {
      this.ruleForm.remindOther = "";
      this.seven = false;
      this.thirdCompany = data.thirdTipTime;
    }
    //
    this.ruleForm.warn = data.storeTip;
  },
  methods: {
    ...mapActions(["otherSetAsync", "otherUpdateAsync"]),
    //
    finalTime() {
      if (this.ruleForm.longtime == "") {
        this.timeLimit = 0;
        this.one = true;
      } else if (this.ruleForm.longtime == "") {
        this.one = false;
      }
    },
    //
    autoUpload() {
      if (this.ruleForm.video == "") {
        this.videoLimit = 0;
        this.two = true;
      } else if (this.ruleForm.video == "") {
        this.two = false;
      }
    },
    //app
    appUpload() {
      if (this.ruleForm.appvideo == "") {
        this.appLimit = 0;
        this.three = true;
      } else if (this.ruleForm.appvideo == "") {
        this.three = false;
      }
    },
    //
    logSwitch() {
      if (this.ruleForm.log == "") {
        this.switchLimit = 0;
        this.four = true;
      } else if (this.ruleForm.log == "") {
        this.four = false;
      }
    },
    //app
    logApp() {
      if (this.ruleForm.applog == "") {
        this.appLogLimit = 0;
        this.five = true;
      } else if (this.ruleForm.applog == "") {
        this.five = false;
      }
    },
    //
    companyRe() {
      if (this.ruleForm.remind == "") {
        this.companyDate = 0;
        this.six = true;
      } else if (this.ruleForm.remind == "") {
        this.six = false;
      }
    },
    //
    otherRe() {
      if (this.ruleForm.remindOther == "") {
        this.thirdCompany = 0;
        this.seven = true;
      } else if (this.ruleForm.remindOther == "") {
        this.seven = false;
      }
    },
    async update() {
      let time=this.timeLimit;
      let upload=this.videoLimit;
      let uploadApp=this.appLimit;
      let log=this.switchLimit;      
      let logApp=this.appLogLimit;
      let companyT=this.companyDate;
      let thirdT=this.thirdCompany;
      let store=this.ruleForm.warn;
      const data=await this.otherUpdateAsync({
        vedioTimeLimit:time,
        isAutoUpload:upload,
        dispatcherUploadLimit:uploadApp,
        appLog:log,
        dispatcherLog:logApp,
        tipTime:companyT,
        thirdTipTime:thirdT,
        storeTip:store
      });
      if (data.code==200) {
        this.$message("");
      }
    }
  }
};
</script>

<style scoped>
.otherSet {
  width: 96%;
  margin: 0 auto;
}
.otherSet > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.otherform {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.otherform input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.otherform .warn {
  margin-left: 0;
}
.otherform span {
  display: inline-block;
  margin-left: 10px;
}
.otherform i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.otherform p {
  color: rgba(153, 153, 153, 1);
}
</style>
<style>
.otherSet .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.otherSet .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.otherSet .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}
.otherform .el-form .el-input {
  width: 100px;
  margin-left: 15px;
}
.otherform .el-form .el-input__inner {
  width: 100px;
}
.otherform .el-form .el-form-item__content {
  width: 800px;
}
.otherform .el-form .address {
  margin-top: 15px;
}
</style>