<template>
  <div class="company">
    <p></p>
    <div class="nav">
      <div class="nav-top">
        <div class="state">
          <span></span>
          <el-select v-model="cooperateValue" @change="cooperation($event)" placeholder="">
            <el-option
              :options="cooperate"
              v-for="item in cooperate"
              :key="item.cooperateValue"
              :label="item.label"
              :value="item.cooperateValue"
            ></el-option>
          </el-select>
        </div>
        <div class="area">
          <span class="demonstration"></span>
          <el-cascader v-model="area" :options="areaOption" @change="handleChange" :clearable="clearArea"></el-cascader>
        </div>
        <div class="timeBetween">
          <span></span>
          <el-date-picker
            v-model="value2"
            type="date"
            placeholder=""
            @change="date1"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
          <span class="to"></span>
          <el-date-picker
            v-model="value4"
            type="date"
            placeholder=""
            @change="date2"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </div>

        <div class="state">
          <span></span>
          <el-select v-model="retrieveValue" @change="retrie($event)" placeholder="">
            <el-option
              :options="retrieve"
              v-for="item in retrieve"
              :key="item.retrieveValue"
              :label="item.label"
              :value="item.retrieveValue"
            ></el-option>
          </el-select>
        </div>
        <input type="text" v-model="writeName" placeholder="" v-if="queryname" />
        <input type="text" v-model="writePwd" placeholder="" v-else-if="querypwd" />
        <button class="search" @click="search"></button>
        <button @click="add"></button>
      </div>
      <div class="nav-bottom">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="bid" label="id" v-if="show"></el-table-column>
          <el-table-column prop="bname" label=""></el-table-column>
          <el-table-column prop="address" label="" width="400px"></el-table-column>
          <el-table-column prop="main_account" label=""></el-table-column>
          <el-table-column prop="validity_time" label="" value-format="yyyy-MM-dd HH:mm:ss"></el-table-column>
          <el-table-column prop="status" label="" :formatter="changeStatus"></el-table-column>
          <el-table-column fixed="right" label="">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="check(scope.row)"></el-button>
              <el-button type="text" size="small" @click="update(scope.row.bid)"></el-button>
              <el-button type="text" size="small" @click="reset = true;rePwd(scope.row.bid)"></el-button>
              <el-button
                type="text"
                size="small"
                v-if="flag==scope.row.status"
                @click="stop = true;repause(scope.row.bid)"
              ></el-button>
              <el-button
                type="text"
                size="small"
                v-else
                @click="startUse = true;restart(scope.row.bid)"
              ></el-button>
              <el-dialog title="" :visible.sync="startUse" append-to-body width="30%">
                <el-form :model="form2">
                  <el-form-item label="" required>
                    <el-date-picker
                      v-model="form2.dateTime"
                      type="datetime"
                      placeholder=""
                      value-format="yyyy-MM-dd HH:mm:ss"
                    ></el-date-picker>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="startUse = false"> </el-button>
                  <el-button type="primary" @click="startUse = false;start();"> </el-button>
                </div>
              </el-dialog>
              <el-dialog title="" :visible.sync="stop" width="30%" append-to-body>
                <span>,</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="stop = false"> </el-button>
                  <el-button type="primary" @click="stop = false;pause()"> </el-button>
                </span>
              </el-dialog>
              <el-dialog title="" :visible.sync="reset" width="30%" append-to-body>
                <span></span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="reset = false"> </el-button>
                  <el-button type="primary" @click="reset = false;pwd()"> </el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNum"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { regionDataPlus, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      area: [],
      areaOption: regionDataPlus,
      cooperate: [
        {
          cooperateValue: "",
          label: ""
        },
        {
          cooperateValue: "",
          label: ""
        },
        {
          cooperateValue: "",
          label: ""
        }
      ],
      retrieve: [
        {
          retrieveValue: "",
          label: ""
        },
        {
          retrieveValue: "",
          label: ""
        }
      ],
      cooperateValue: "",
      value2: "",
      retrieveValue: "",
      value4: "",
      show: false,
      flag: false,
      startUse: false,
      form2: {
        dateTime: ""
      },
      formLabelWidth: "120px",
      stop: false,
      reset: false,
      writePwd: "",
      writeName: "",
      queryname: true,
      querypwd: false,
      clearArea:false
    };
  },
  mounted() {
    this.formAsync();
    this.locationAsync({
      parentId: 1
    });
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions([
      "formAsync",
      "searchAsync",
      "reStartAsync",
      "pauseAsync",
      "locationAsync",
      "rePasswordAsync"
    ]),
    //
    cooperation(event, item) {
      if (event == "") {
        this.coop = 0;
      } else if (event == "") {
        this.coop = 1;
      }else if (event == "") {
        this.coop = "";
      }
    },
    //
    handleChange(value) {
     let province;
      let city;
      let area;
      // console.log(value[0]);
      if (CodeToText[value[0]] == "") {
        province = "";
        city = "";
        area = "";
        this.location = province + city + area;
      } else {
        province = CodeToText[value[0]];

        if (CodeToText[value[1]] == "") {
          city = "";
          area = "";
          this.location = province + city + area;
        } else {
          city = CodeToText[value[1]];
          if (CodeToText[value[2]] == "") {
            area = "";
            this.location = province + city + area;
          } else {
            area = CodeToText[value[2]];
            this.location = province + city + area;
          }
        }
      }
    },
    //
    date1(value) {
      this.startTime = value;
      // console.log(this.startTime);
    },
    date2(value) {
      this.endTime = value;
      // console.log(this.endTime);
    },
    //
    retrie(event, item) {
      if (event == "") {
        this.queryname = true;
        this.querypwd = false;
        console.log(event);
      } else if (event == "") {
        this.queryname = false;
        this.querypwd = true;
        console.log(event);
      }
    },
    //
    add() {
      this.$router.push(`/Index/addition`);
    },
    //
    check(prop) {
      sessionStorage.setItem("company", JSON.stringify(prop));
      this.$router.push(`/Index/companyDetails`);
    },
    //
    update(prop) {
      sessionStorage.setItem("updateID", JSON.stringify(prop));
      this.$router.push(`/Index/companyUpdate`);
    },
    changeStatus(row, column) {
      switch (row.status) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
      }
    },

    //
    restart(prop) {
      sessionStorage.setItem("ID", JSON.stringify(prop));
    },
    async start() {
      let id = JSON.parse(sessionStorage.getItem("ID"));
      let dateTime = this.form2.dateTime;
      const data = await this.reStartAsync({
        bid: id,
        endTime: dateTime
      });
      if (data.code === 200) {
        this.flag == true;
        this.$message("");
        this.formAsync();
      } else if (data.code === 206) {
        this.$message(",,");
      }
    },
    //
    repause(prop) {
      sessionStorage.setItem("BID", JSON.stringify(prop));
    },
    async pause() {
      let id = JSON.parse(sessionStorage.getItem("BID"));
      const data = await this.pauseAsync({
        bid: id
      });
      if (data.code === 200) {
        this.flag == false;
        this.$message("");
        this.formAsync();
      } else if (data.code === 201) {
        this.$message("");
      }
    },
    //
    rePwd(prop) {
      sessionStorage.setItem("pID", JSON.stringify(prop));
    },
    async pwd() {
      let pid = JSON.parse(sessionStorage.getItem("pID"));
      const data = await this.rePasswordAsync({ bid: pid });
      if (data.code === 200) {
        this.$message("");
      }
    },
    //
    search() {
      let location = this.location;
      let start = this.startTime;
      let end = this.endTime;
      let coop = this.coop;
      let writePwd = this.writePwd;
      let writeName = this.writeName;
      this.$store.commit("company/initLocation", location);
      this.$store.commit("company/initStart", start);
      this.$store.commit("company/initEnd", end);
      this.$store.commit("company/initCoop", coop);
      this.$store.commit("company/initWritePwd", writePwd);
      this.$store.commit("company/initWriteName", writeName);   
      this.searchAsync();
    },
    //
    handleCurrentChange(val) {
      this.$store.commit("company/changePage", val);
      this.formAsync();
    }
  }
};
</script>

<style scoped>
.company {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.nav {
  width: 100%;
  height: 800px;
}
.nav-top {
  width: 100%;
  height: 100px;
  display: flex;
}
input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 150px;
  height: 34px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 20px;
  margin-top: 1px;
}
.nav-top button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 100px;
  height: 34px;
  text-align: center;
  margin-left: 30px;
  margin-top: 3px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.nav-top button:hover {
  cursor: pointer;
}
.nav-top button:nth-of-type(1) {
  width: 80px;
}
.nav-top > div {
  margin-right: 15px;
}
.nav-top span {
  margin-right: 15px;
  font-size: 14px;
  font-weight: 500;
}
.nav-bottom {
  width: 96%;
  height: 600px;
  margin: 0 auto;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}

.nav-top .el-input {
  width: 150px;
}
.timeBetween .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 160px;
}
.to {
  margin-left: 15px;
}
</style>

<style>
.company .nav-top .state .el-input {
  width: 150px;
}
.area .el-input {
  width: 220px;
}
</style>