<template>
  <div class="updateContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="220px"
        class="demo-ruleForm"
      >
        <div class="first">
          <el-form-item label="" prop="bname">
            <el-input v-model="ruleForm.bname" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mainAccount">
            <el-input v-model="ruleForm.mainAccount" placeholder="3-10" :disabled="true"></el-input>
          </el-form-item>
          <p class="ptips"></p>
          <el-form-item label="" prop="address">
            <!--  -->
            <label>
              <el-cascader
                v-model="ruleForm.area"
                :options="areaOption"
                @change="handleChange"
                clearable
              ></el-cascader>
            </label>
            <el-input
              v-model="ruleForm.address"
              placeholder=""
              @input="inputOnInput($event)"
              id="suggestId"
            ></el-input>
          </el-form-item>
          <el-form-item style="marginTop:-15px;">
            <baidu-map
              :center="center"
              :zoom="zoom"
              @ready="handler"
              class="baiduMap"
              :scroll-wheel-zoom="true"
              id="l-map"
            >
              <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
              <bm-local-search
                :keyword="ruleForm.address"
                :auto-viewport="true"
                :panel="false"
                @searchcomplete="getGps"
              ></bm-local-search>
            </baidu-map>
          </el-form-item>
          <el-form-item label="" prop="personInCharge">
            <el-input v-model="ruleForm.personInCharge" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="picPhone">
            <el-input v-model="ruleForm.picPhone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="validityTime">
            <el-date-picker
              v-model="ruleForm.validityTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="ruleForm.status" placeholder="" @change="sta">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input
              type="textarea"
              v-model="ruleForm.description"
              placeholder="500"
              maxlength="500"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="deviceNumLimit">
            <el-input v-model="ruleForm.deviceNumLimit"></el-input>
          </el-form-item>
        </div>
        <div class="second">
          <el-form-item label="" required>
            <div class="checkGroup">
              <el-checkbox label="" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span></span>
            <i>1440</i>
          </el-form-item>
          <el-form-item label="" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i></i>
          </el-form-item>
          <el-form-item label="app" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>wifiwifi</i>
          </el-form-item>
          <el-form-item label="" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="app" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i></i>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="add"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
import { log } from "util";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        status: "",
        description: "",
        deviceNumLimit: 100,
        validityTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 60,
        upload: "",
        videoLimit: 1000,
        app: "",
        appLimit: 500,
        switch: "",
        switchLimit: 20,
        log: "",
        appLogLimit: 20,
        save: "",
        saveLimit: 100
      },
      lons: "",
      lats: "",
      areaOption: regionData,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      location: "",
      keyword: "",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 20, message: "20", trigger: "blur" }
        ],
        mainAccount: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "3-10",
            trigger: "blur"
          }
        ],
        address: [
          { required: true, message: "", trigger: "blur" }
        ],
        personInCharge: [
          { required: true, message: "", trigger: "blur" }
        ],
        picPhone: [
          { required: true, message: "", trigger: "blur" }
        ],
        validityTime: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "blur" }
        ],
        description: [{ max: 500, message: "500", trigger: "blur" }],
        deviceNumLimit: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("updateID"));
    const data = await this.companyDetailAsync(id);
    this.did = data.diySettingMapper.did;
    let company = Object.assign(data.businessManager, data.diySettingMapper);
    let newfrom = Object.assign(this.ruleForm, company);
    console.log(company);
    this.setlongitude = company.longitude;
    this.setlatitude = company.latitude;
    this.ruleForm.address = company.address;
    //
    if (company.status == 0) {
      this.ruleForm.status = "";
    } else if (company.status == 1) {
      this.ruleForm.status = "";
    }
    //
    if (company.isCall == "0") {
      this.ruleForm.voice = "0";
    } else {
      this.ruleForm.voice = "";
    }
    if (company.vedio == "0") {
      this.ruleForm.see = "0";
    } else {
      this.ruleForm.see = "";
    }
    if (company.talkback == "0") {
      this.ruleForm.talk = "0";
    } else {
      this.ruleForm.talk = "";
    }
    if (company.mapRange == "0") {
      this.ruleForm.rail = "0";
    } else {
      this.ruleForm.rail = "";
    }
    if (company.trail == "0") {
      this.ruleForm.track = "0";
    } else {
      this.ruleForm.track = "";
    }
    if (company.broadcast == "0") {
      this.ruleForm.broad = "0";
    } else {
      this.ruleForm.broad = "";
    }
    if (company.listener == "0") {
      this.ruleForm.detect = "0";
    } else {
      this.ruleForm.detect = "";
    }
    //
    if (company.vedioTimeLimitSys == 0) {
      this.ruleForm.time = "";
    } else if (company.vedioTimeLimitSys == 1) {
      this.ruleForm.time = "";
    } else if (company.vedioTimeLimitSys == 2) {
      this.ruleForm.time = "";
      this.ruleForm.timeLimit = company.vedioTimeLimit;
      this.one = false;
    }
    //
    if (company.isAutoUploadSys == 0) {
      this.ruleForm.upload = "";
    } else if (company.isAutoUploadSys == 1) {
      this.ruleForm.upload = "";
    } else if (company.isAutoUploadSys == 2) {
      this.ruleForm.upload = "";
      this.ruleForm.videoLimit = company.isAutoUpload;
      this.two = false;
    }
    //app
    if (company.dispatcherUploadLimitSys == 0) {
      this.ruleForm.app = "";
    } else if (company.dispatcherUploadLimitSys == 1) {
      this.ruleForm.app = "";
    } else if (company.dispatcherUploadLimitSys == 2) {
      this.ruleForm.app = "";
      this.ruleForm.appLimit = company.dispatcherUploadLimit;
      this.three = false;
    }
    //
    if (company.appLogSys == 0) {
      this.ruleForm.switch = "";
    } else if (company.appLogSys == 1) {
      this.ruleForm.switch = "";
    } else if (company.appLogSys == 2) {
      this.ruleForm.switch = "";
      this.ruleForm.switchLimit = company.appLog;
      this.four = false;
    }
    //app
    if (company.dispatcherLogSys == 0) {
      this.ruleForm.log = "";
    } else if (company.dispatcherLogSys == 1) {
      this.ruleForm.log = "";
    } else if (company.dispatcherLogSys == 2) {
      this.ruleForm.log = "";
      this.ruleForm.appLogLimit = company.dispatcherLog;
      this.five = false;
    }
    //
    if (company.storeTipSys == 0) {
      this.ruleForm.save = "";
    } else if (company.storeTipSys == 1) {
      this.ruleForm.save = "";
      this.ruleForm.saveLimit = company.storeTip;
      this.six = false;
    }
  },
  methods: {
    ...mapActions(["updateCompanyAsync", "formAsync", "companyDetailAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.location = province + city + area;
      this.ruleForm.address = this.location;
      this.changelocationflag = true;
    },
    //
    sta() {
      this.staus = this.ruleForm.status;
    },
    //
    finalTime() {
      if (this.ruleForm.time == "") {
        this.videoTime = 0;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //
    videoUpload() {
      console.log(this.ruleForm.upload);
      if (this.ruleForm.upload == "") {
        this.uploadVideo = 0;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //app
    appUpload() {
      if (this.ruleForm.app == "") {
        this.appVideo = 0;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "") {
        this.appVideo = 2;
        this.three = false;
      }
    },
    //
    logSwitch() {
      if (this.ruleForm.switch == "") {
        this.switchLog = 0;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "") {
        this.switchLog = 2;
        this.four = false;
      }
    },
    //app
    appLog() {
      if (this.ruleForm.log == "") {
        this.logApp = 0;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "") {
        this.logApp = 2;
        this.five = false;
      }
    },
    //
    savation() {
      if (this.ruleForm.save == "") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "") {
        this.saveSet = 1;
        this.six = false;
      }
    },
    inputOnInput(e) {
      this.$forceUpdate();
    },
    //
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
      console.log(results.Br[0].point);
      this.gpsLocation = results.Br[0].point;
    },
    //
    async add() {
      let name = this.ruleForm.bname;
      let acount = this.ruleForm.mainAccount;
      this.addrDetail = this.ruleForm.address;
      let addrDetail = this.addrDetail;
      let charge = this.ruleForm.personInCharge;
      let phone = this.ruleForm.picPhone;
      let dateTime = this.ruleForm.validityTime + " " + "00:00:00";
      let coStatus = this.staus;
      let others = this.ruleForm.description;
      let limit = this.ruleForm.deviceNumLimit;
      let voice = this.ruleForm.voice;
      let see = this.ruleForm.see;
      let talk = this.ruleForm.talk;
      let rail = this.ruleForm.rail;
      let track = this.ruleForm.track;
      let broad = this.ruleForm.broad;
      let detect = this.ruleForm.detect;
      let time = this.videoTime;
      let timeLimit = this.ruleForm.timeLimit;
      let uploadVideo = this.uploadVideo;
      let videoLimit = this.ruleForm.videoLimit;
      let appVideo = this.appVideo;
      let appLimit = this.ruleForm.appLimit;
      let switchLog = this.switchLog;
      let switchLimit = this.ruleForm.switchLimit;
      let logApp = this.logApp;
      let appLogLimit = this.ruleForm.appLogLimit;
      let saveSet = this.saveSet;
      let saveLimit = this.ruleForm.saveLimit;
      //biddid
      let didUpdate = this.did;
      let idUpdate = JSON.parse(sessionStorage.getItem("updateID"));
      let lon;
      let lat;
      if ((this.changelocationflag = true)) {
        lon = this.gpsLocation.lng;
        lat = this.gpsLocation.lat;
      } else {
        lon = this.setlongitude;
        lat = this.setlatitude;
      }
      console.log(lon, lat);

      const data = await this.updateCompanyAsync({
        bname: name,
        mainAccount: acount,
        address: addrDetail,
        personInCharge: charge,
        picPhone: phone,
        validityTime: dateTime,
        status: coStatus,
        description: others,
        deviceNumLimit: limit,
        isCall: voice,
        vedio: see,
        talkback: talk,
        mapRange: rail,
        trail: track,
        broadcast: broad,
        listener: detect,
        vedioTimeLimitSys: time,
        vedioTimeLimit: timeLimit,
        isAutoUploadSys: uploadVideo,
        isAutoUpload: videoLimit,
        dispatcherUploadLimitSys: appVideo,
        dispatcherUploadLimit: appLimit,
        appLogSys: switchLog,
        appLog: switchLimit,
        dispatcherLogSys: logApp,
        dispatcherLog: appLogLimit,
        storeTipSys: saveSet,
        storeTip: saveLimit,
        bid: idUpdate,
        did: didUpdate,
        longitude: lon,
        latitude: lat
      });
      if (data.code == 200) {
        this.$message("");
        this.$router.push(`/Index/company`);
        this.formAsync();
      } else if (data.code == 501) {
        this.$message("");
      } else if (data.code == 502) {
        this.$message("");
      } else {
        this.$message("");
      }
    }
  }
};
</script>

<style scoped>
.updateContainer {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.form p {
  color: rgba(153, 153, 153, 1);
}
.form .el-form .finalVideo {
  display: flex;
}
/*  */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
.address {
  margin-top: 20px;
}
.map {
  width: 400px;
  height: 400px;
}
.ptips {
  color: rgba(153, 153, 153, 1);
  margin-left: 220px;
  font-size: 14px;
}
</style>
<style>
.updateContainer .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.updateContainer .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.updateContainer .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}

.form .el-form .first .el-input__inner {
  width: 400px;
}
.form .el-form .second .el-input__inner {
  width: 120px;
}
.form .el-form .second .el-input {
  width: 120px;
  margin-left: 20px;
}
.form .el-form .first .el-form-item__content {
  width: 800px;
}
.form .el-form .second .el-form-item__content {
  width: 1200px;
}
.form .el-form .address {
  margin-top: 15px;
}
.updateContainer .el-textarea {
  width: 400px;
}
.updateContainer .el-cascader {
  margin-bottom: 20px;
}
/*  */
.BMap_cpyCtrl {
  display: none;
}
.anchorBL {
  display: none;
}
</style>